<script>
  import { colors } from './utils.js';

  export let bgimage;
  export let bgfixed = false;
  export let theme;

  let style = '';

  if (bgimage) {
    style += `background-image: url(${bgimage});`;
  } else {
    style += 'background-image: none;';
  }

  if (bgfixed) {
    style += ' background-attachment: fixed;';
  }

  if (theme) {
    style += ` color: ${colors[theme]['text']}; background-color: ${colors[theme]['background']};`
  }
</script>

<header style="{style}">
	<div class="col-medium height-full middle center">
		<headline>
			<slot></slot>
		</headline>
	</div>
</header>